import React, { useEffect, useState, useRef } from "react";
import CloseIcon from '@mui/icons-material/Close';
import moment from 'moment'


import Video from "../Video";

import styles from './ScheduleItem.module.scss';
import { getImg, useWindowSize } from "../../utils/Helper";

// const v1 = 'https://res.cloudinary.com/dssvrf9oz/video/upload/v1635662987/pexels-pavel-danilyuk-5359634_1_gmixla.mp4';

const ScheduleModal = (props) => {
	const [windowWidth, windowHeight] = useWindowSize();

	return (
		<div className={styles.schedule_modal} style={{ width: windowWidth + 'px', height: windowHeight + 'px' }}>
			<div className={styles.card}>
				<div className={styles.close_btn} onClick={() => props.hideModal()}>
					<CloseIcon />
				</div>
				<div className={styles.title}>
					{props.value.title}
				</div>
				<div className={styles.content}>
					{props.value.content}
				</div>
				<div className={styles.video_container}>
					<Video width={638} id={props.value.id} value={{video: props.value.video, lastTime: 0}} />
				</div>
				<div className={styles.footer}>
					<div className={styles.first}>
						<img src={props.value.avatarImg} alt="Logo" />
						<div className={styles.footer_text}>
							@{props.value.name}
						</div>
					</div>
					<div className={styles.second}>
						{moment(new Date(props.value.date)).format('DD/MM/YYYY')}&nbsp;&nbsp;&nbsp;&nbsp;{`${moment(new Date(props.value.date)).format('HH:mm')}${(Number(moment(new Date(props.value.date)).format('HH')) > 12 ? 'pm' : 'am')}`}
					</div>
				</div>
			</div>
		</div>
	)
}


const ScheduleItem = (props) => {
	const [groupNum, setGroupNum] = useState(1);
	const [searchVal, setSearchVal] = useState('');
	const [showModal, setShowModal] = useState(false);

	const hideModal = () => {
		setShowModal(false)
	}

	return (
		<>
			<div className={styles.schedule_item} onClick={() => setShowModal(true)}>
				<div className={styles.title}>
					{props.value.title}
				</div>
				<div className={styles.description}>
					{props.value.subtitle}
				</div>
				<div className={styles.item_footer}>
					<div className={styles.left}>
						<div className={styles.footer_img}>
							<img src={props.value.avatarImg} alt="Logo" />
						</div>
						<div className={styles.footer_text}>
							@{props.value.name}
						</div>
					</div>
					<div className={styles.right}>
						{moment(new Date(props.value.date)).format('DD/MM/YYYY')}&nbsp;&nbsp;&nbsp;&nbsp;{`${moment(new Date(props.value.date)).format('HH:mm')}${(Number(moment(new Date(props.value.date)).format('HH')) > 12 ? 'pm' : 'am')}`}
					</div>
				</div>
			</div >
			{
				showModal && <ScheduleModal value={props.value} hideModal={hideModal} />
			}
		</>
	)
}

export default ScheduleItem