import React, { useState, useRef, useEffect } from "react";
import { Link, useNavigate } from "react-router-dom";
import { useSelector, useDispatch } from 'react-redux'

import CloseIcon from '@mui/icons-material/Close';

import styles from './NonePage.module.scss'


import agent from '../../api/'

import {
  startAction,
  endAction,
  showToast
} from '../../actions/common'
import { logout } from '../../actions/auth'

const NonePage = () => {

  const navigate = useNavigate()
  const dispatch = useDispatch()
  const auth = useSelector(state => state.auth)

  const [isShow, setIsShow] = useState(false)

  window.dragon.setCustomizeData = function (data) {
    const setData = async (data) => {
      const res = await agent.common.setCustomizeModel(
        auth.currentUser.id,
        data
      )
      if (res.data.success) {
        //setIsShow(true)
        dispatch(showToast('success', res.data.message))
      }
      else dispatch(showToast('error', res.data.message))
      setIsShow(true)
    }
    setData(data)
  }

  useEffect(() => {
  }, [])

  window.dragon.goHomePage = function () {
    navigate('/home')
  }

  return (
    <>
      {
        isShow &&
        <div className={styles.main}>
          <div className={styles.message_card}>
            <div className={styles.rel_container}>
              <div className={styles.message}>
                Seu Avatar foi modificado com sucesso!
              </div>
              <div className={styles.close} onClick={() => setIsShow(false)}>
                <CloseIcon className={styles.close_btn} />
              </div>
            </div>
          </div>
        </div>
      }
    </>
  )
}

export default NonePage