import {
  START_GAME,
  START_ACTION,
  END_ACTION,
  SHOW_TOAST,
  HIDE_TOAST,
  SHOW_CHAT,
  SHOW_SMALL
} from '../constants/actionTypes/common'

const INITIAL_STATE = {
  loadLogin: false,
  loading: false,
  showToast: false,
  msgType: '',
  msgContent: '',
  showChat: false,
  showSmall: false
}

const reducer = (state = INITIAL_STATE, action) => {
  switch (action.type) {
    case START_GAME:
      return {
        ...state,
        loadLogin: true
      }
    case START_ACTION:
      return {
        ...state,
        loading: true
      }
    case END_ACTION:
      return {
        ...state,
        loading: false
      }
    case SHOW_TOAST:
      return {
        ...state,
        showToast: true,
        msgType: action.msgType,
        msgContent: action.content
      }
    case HIDE_TOAST:
      return {
        ...state,
        showToast: false
      }
    case SHOW_CHAT:
      return {
        ...state,
        showChat: action.isShow
      }
    case SHOW_SMALL:
      return {
        ...state,
        showSmall: action.isSmall
      }
    default:
      return state
  }
}

export default reducer