import {
  AUTH_LOGIN,
  AUTH_LOGOUT
} from '../constants/actionTypes/auth'

const INITIAL_STATE = {
  currentUser: {
    id: 0,
    username: '',
    email: '',
    token: ''
  }
}

const reducer = (state = INITIAL_STATE, action) => {
  switch (action.type) {
    case AUTH_LOGIN:
      return {
        ...state,
        currentUser: {
          id: action.user.data.id,
          username: action.user.data.username,
          email: action.user.data.email,
          token: action.user.token
        }
      }
    case AUTH_LOGOUT:
      return {
        ...state,
        currentUser: {
          email: '',
          token: ''
        }
      }
    default:
      return state
  }
}

export default reducer