import React, { useState, useEffect } from "react";
import { Link, useNavigate } from 'react-router-dom';
import { useSelector, useDispatch } from 'react-redux'

import CreateIcon from '@mui/icons-material/Create';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faLinkedinIn, faFacebookF, faInstagram } from '@fortawesome/free-brands-svg-icons';

import Circle from 'react-circle';
import LineChart from "../../components/SmallLineChart/LineChart";

import styles from './Profile.module.scss';
import { getImg, useWindowSize } from "../../utils/Helper";

import {
  startAction,
  endAction,
  showToast
} from '../../actions/common'
import agent from '../../api/'
import { logout } from "../../actions/auth";

const role = ['Aux. Administrativo', 'Aux. Administrativo1', 'Aux. Administrativo2']

const Profile = (props) => {

  const auth = useSelector(state => state.auth)
  const dispatch = useDispatch()
  const navigate = useNavigate()

  const [avatarImg, setAvatarImg] = useState('')
  const [bio, setBio] = useState({})
  const [account, setAccount] = useState({})
  const [chartData, setChartData] = useState([]);
  const [progress, setProgress] = useState(0);


  const getCustomizeData = async () => {
    try {
      const res = await agent.common.getCustomizeModel(auth.currentUser.id)
      if (res.data.success) {
        navigate('/none')
        window.dragon.clickCustomize(res.data.data)
      }
    } catch (error) {
      if (error.response !== undefined && error.response.status === 401) {
        dispatch(showToast('error', error.response.data.message))
        if (error.response.data.message == 'User token expired.') {
          localStorage.removeItem('token')
          dispatch(logout())
          window.dragon.stopUnityGame()
          navigate('/')
        }
      }
    }
  }

  useEffect(() => {
    const getData = async () => {
      dispatch(startAction())
      try {
        const resAvatarImg = await agent.auth.getAccountImg(auth.currentUser.id)
        const resBio = await agent.auth.getBioData(auth.currentUser.id)
        const resAccount = await agent.auth.getAccountData(auth.currentUser.id)
        const resProgress = await agent.common.getProgress(auth.currentUser.id)
        const resChartData = await agent.common.getChartData(auth.currentUser.id, 1)
        if (resAvatarImg.data.success) setAvatarImg(resAvatarImg.data.data.avatarImg)
        if (resBio.data.success) setBio({ ...resBio.data.data })
        if (resAccount.data.success) setAccount({ ...resAccount.data.data })
        if (resProgress.data.success) setProgress(resProgress.data.data.progress)
        if (resChartData.data.success) {
          let newArray = []
          resChartData.data.data.map((item, idx) =>
            newArray.push({ label: item.xLabel, x: idx, y: item.Value })
          )
          setChartData([...newArray])
        }
        dispatch(endAction())
      } catch (error) {
        if (error.response.status === 401) {
          dispatch(endAction())
          dispatch(showToast('error', error.response.data.message))
          if (error.response.data.message == 'User token expired.') {
            localStorage.removeItem('token')
            dispatch(logout())
            window.dragon.stopUnityGame()
            navigate('/')
          }
        }
      }
    }
    getData()
  }, [])

  return (
    <div className={styles.main}>
      <div className={styles.profile}>
        <div className={styles.profile_container}>
          <div className={styles.user_description_card}>

            <div className={styles.name}>
              {bio.name}
            </div>
            <div className={styles.id}>
              {bio.text}
            </div>
            <div className={styles.content}>
              {bio.bio}
            </div>
            <div className={styles.edit_btn}>
              <Link to={'/editprofile'}>
                <CreateIcon />
              </Link>
            </div>
          </div>
          <div className={styles.user_info_card}>
            <div className={styles.left_info}>
              <div className={styles.title}>
                Email
              </div>
              <div className={styles.content}>
                {account.email}
              </div>
              <div className={styles.title}>
                Cargo
              </div>
              <div className={styles.content}>
                {role[account.office - 1]}
              </div>
              <div className={styles.title}>
                Ranking
              </div>
              <div className={styles.content}>
                {account.ranking}
              </div>
              <div className={styles.title}>
                Senha
              </div>
              <div className={styles.content}>
                {
                  Array.from(Array(account.password === undefined || account.password === null ? 0 : account.password.length).keys()).map(() => {
                    return '*'
                  })
                }
              </div>
            </div>
            <div className={styles.right_info}>
              <div className={styles.header}>
                Redes sociais:
              </div>
              <div className={styles.social_icons}>
                <div className={styles.icon}>
                  {
                    account.facebook !== '' ?
                      <Link to={`/${account.facebook}`}>
                        <img src={getImg('image/icons/facebook.png')} alt="icon" />
                      </Link> : ''
                  }
                </div>
                <div className={styles.icon}>
                  {
                    account.instagram !== '' ?
                      <Link to={`/${account.instagram}`}>
                        <img src={getImg('image/icons/instagram.png')} alt="icon" />
                      </Link> : ''
                  }
                </div>
                <div className={styles.icon}>
                  {
                    account.linkedin !== '' ?
                      <Link to={`/${account.linkedin}`}>
                        <img src={getImg('image/icons/linkedin.png')} alt="icon" />
                      </Link> : ''
                  }
                </div>
                <div className={styles.icon}>
                  {
                    account.twitter !== '' ?
                      <Link to={`/${account.twitter}`}>
                        <img src={getImg('image/icons/twiter.png')} alt="icon" />
                      </Link> : ''
                  }
                </div>
              </div>
              {/* <div className={styles.social_icons}>
                <div className={styles.icon}>
                  <FontAwesomeIcon icon={faLinkedinIn} />
                </div>
                <div className={styles.icon}>
                  <FontAwesomeIcon icon={faFacebookF} />
                </div>
                <div className={styles.icon}>
                  <FontAwesomeIcon icon={faInstagram} />
                </div>
              </div> */}
            </div>
            <div className={styles.edit_btn}>
              <Link to={'/editinfo'}>
                <CreateIcon />
              </Link>
            </div>
          </div>
          <div className={styles.user_chart_card}>
            <div className={styles.left_chart}>
              <div className={styles.title}>
                Progresso
              </div>
              <div className={styles.chart_container}>
                <Circle
                  animate={true}
                  animationDuration="1s"
                  responsive={false}
                  size="147"
                  lineWidth="50"
                  progress={`-${progress}`}
                  progressColor="#E53222"
                  bgColor="#FDF2D0"
                  textColor="#FFF"
                  roundedStroke={true}
                  showPercentage={true}
                  showPercentageSymbol={true}
                />
                <div className={styles.percent_text}>
                  {progress}%
                </div>
              </div>
              <div className={styles.under_text}>
                Lorem Ipsum
              </div>
            </div>

            <div className={styles.right_chart}>
              <LineChart
                width={255}
                height={200}
                data={chartData}
                horizontalGuides={4}
                precision={0}
                verticalGuides={0}
              />
            </div>
          </div>
        </div>
      </div>

      <div className={styles.avatar_container}>
        <div className={styles.rel_container}>
          {/* <img src={avatarImg} alt="Logo" /> */}
          <img src={getImg('image/back/avatar.png')} alt="Logo" />
          <div className={styles.edit_btn} onClick={() => getCustomizeData()}>
            <CreateIcon />
          </div>
        </div>
      </div>

      <Link to={'/home'}>
        <div className={styles.home_btn}>
          <img src={getImg('image/icons/home.png')} alt="icon" />
        </div>
      </Link>
    </div >
  )
}

export default Profile