import React, { useEffect, useState } from "react";
import { Link, useNavigate } from 'react-router-dom';
import { useSelector, useDispatch } from 'react-redux'
import Badge from '@mui/material/Badge';
import NotificationsIcon from '@mui/icons-material/Notifications';

import styles from './Menuitem.module.scss';
import { getImg } from "../../../utils/Helper";
import { logout } from "../../../actions/auth";

const Menuitem = (props) => {

	const navigate = useNavigate()
	const dispatch = useDispatch()

	const logout_click = () => {
		localStorage.removeItem('token')
		dispatch(logout())
		window.dragon.stopUnityGame()
		navigate('/')
	}

	return (
		<>
			{props.small &&
				<div className={styles.small_menuitem}>
					{props.active &&
						<Link to={`/${props.value.page}`}>
							<button onClick={() => props.closeInterval()} className={`${styles.small_menuitem_btn} ${styles.active}`}>
								<img className={styles.small_menuitem_icon} src={getImg(`${props.value.img}`)} alt="menuitem" />
							</button>
						</Link>
					}
					{!props.active && props.value.id !== 5 && props.value.id !== 7 && props.value.id !== 8 &&
						<>
							{
								props.value.page !== '' ?
									<Link to={`/${props.value.page}`}>
										<button onClick={() => props.closeInterval()} className={styles.small_menuitem_btn}>
											<img className={styles.small_menuitem_icon} src={getImg(`${props.value.img}`)} alt="menuitem" />
										</button>
									</Link>
									:
									<button className={styles.small_menuitem_btn}>
										<img className={styles.small_menuitem_icon} src={getImg(`${props.value.img}`)} alt="menuitem" />
									</button>
							}
						</>
					}
					{props.value.id === 5 &&
						<button className={styles.small_menuitem_btn} onClick={() => props.toggleNotification()}>
							<Badge className={styles.badge} badgeContent={props.notification}>
								<NotificationsIcon />
							</Badge>
						</button>
					}
					{
						props.value.id === 7 &&
						<button className={styles.small_menuitem_btn} onClick={() => props.getCustomizeData()}>
							<img className={styles.small_menuitem_icon} src={getImg(`${props.value.img}`)} alt="menuitem" />
						</button>
					}
					{
						props.value.id === 8 &&
						<button className={styles.small_menuitem_btn} onClick={() => logout_click()}>
							<img className={styles.small_menuitem_icon} src={getImg(`${props.value.img}`)} alt="menuitem" />
						</button>
					}
				</div>
			}
			{!props.small &&
				<div className={styles.menuitem}>
					{props.active &&
						<div className={styles.hover_btn}>
							<Link to={`/${props.value.page}`}>
								<button onClick={() => props.closeInterval()} className={`${styles.menuitem_btn} ${styles.active}`}>
									{props.value.id !== 5 &&
										<img className={styles.menuitem_icon} src={getImg(`${props.value.img}`)} alt="menuitem" />
									}
									{props.value.text}
								</button>
								<div className={`${styles.left_btn} ${styles.active}`}>
								</div>
							</Link>
						</div>
					}
					{!props.active && props.value.id !== 5 && props.value.id !== 7 && props.value.id !== 8 &&
						<div className={styles.nohover_btn}>
							{
								props.value.page !== '' ?
									<Link to={`/${props.value.page}`}>
										<button onClick={() => props.closeInterval()} className={styles.menuitem_btn}>
											<img className={styles.menuitem_icon} src={getImg(`${props.value.img}`)} alt="menuitem" />
											{props.value.text}
										</button>
										<div className={styles.left_btn}>
										</div>
									</Link>
									:
									<>
										<button className={styles.menuitem_btn}>
											<img className={styles.menuitem_icon} src={getImg(`${props.value.img}`)} alt="menuitem" />
											{props.value.text}
										</button>
										<div className={styles.left_btn}>
										</div>
									</>
							}

						</div>
					}
					{props.value.id === 5 &&
						<div className={styles.nohover_btn} onClick={() => props.toggleNotification()}>
							<button className={styles.menuitem_btn}>
								<Badge className={styles.badge} badgeContent={props.notification}>
									<NotificationsIcon />
								</Badge>
								{props.value.text}
							</button>
							<div className={styles.left_btn}>
							</div>
						</div>
					}
					{
						props.value.id === 7 &&
						<div className={styles.nohover_btn}>
							<button className={styles.menuitem_btn} onClick={() => props.getCustomizeData()}>
								<img className={styles.menuitem_icon} src={getImg(`${props.value.img}`)} alt="menuitem" />
								{props.value.text}
							</button>
							<div className={styles.left_btn}>
							</div>
						</div>
					}
					{
						props.value.id === 8 &&
						<div className={styles.nohover_btn}>
							<button className={styles.menuitem_btn} onClick={() => logout_click()}>
								<img className={styles.menuitem_icon} src={getImg(`${props.value.img}`)} alt="menuitem" />
								{props.value.text}
							</button>
							<div className={styles.left_btn}>
							</div>
						</div>
					}
				</div>
			}
		</>
	)
}

export default Menuitem