import React, { useEffect, useState } from "react";
import { Link } from 'react-router-dom';
import GroupsIcon from '@mui/icons-material/Groups';

import styles from './UserCard.module.scss';
import { getImg } from "../../../utils/Helper";

const contentsMenu =
	[
		'Todos os cursos',
		'Marketing e Vendas',
		'Treinamento de Vendas',
		'Endomarketing',
		'Gesto de Tempo',
		'Gesto Financeira',
		'Digitao',
		'Produtividade',
		'Design Thinking'
	];

const backImg = [
	'image/back/user_back1.png',
	'image/back/user_back2.png',
	'image/back/user_back3.png',
	'image/back/user_back4.png'
]

const UserCard = (props) => {

	return (

		<div className={styles.user_card}>
			<div className={styles.header_img_container}>
				<img src={getImg(`${backImg[`${props.index % 4}`]}`)} alt="Logo" />
				<Link to={`/university/instructorinfo/${props.value.id}`} >
					<img src={props.value.avatarImg} alt="Logo" />
				</Link>
			</div>
			<div className={styles.content}>
				<div className={styles.title}>
					{props.value.name}
				</div>
				<div className={styles.subtitle}>
					{contentsMenu[props.value.type]}
				</div>
				<div className={styles.action}>
					<div className={styles.info}>
						<img src={getImg('image/icons/people.png')} className={styles.info_icon} alt="icon" />
						{props.value.sudentCount}
					</div>
					<div className={styles.btn_container}>
						{
							props.value.followStauts ?
								<div className={`${styles.action_btn} ${styles.active}`} onClick={() => props.activeInstructor(props.typeIndex, props.index, false)}>
									Seguindo
								</div> :
								<div className={styles.action_btn} onClick={() => props.activeInstructor(props.typeIndex, props.index, true)}>
									+&nbsp;&nbsp;Seguir
								</div>
						}
					</div>
				</div>
			</div>
		</div>
	)
}

export default UserCard