import React, { useState, useEffect } from "react";
import { useDispatch } from 'react-redux'
import { useNavigate, Link } from 'react-router-dom'

import styles from './Login.module.scss';
import { useWindowSize } from "../../utils/Helper";

import agent from '../../api/'

import {
  startAction,
  endAction,
  showToast,
  startGame
} from '../../actions/common'

import { login } from '../../actions/auth'

const Login = (props) => {

  const dispatch = useDispatch()
  const navigate = useNavigate()
  const [windowWidth, windowHeight] = useWindowSize();

  const [email, setEmail] = useState('')
  const [password, setPassword] = useState('')
  const [emailInputError, setEmailInputError] = useState(false)
  const [passwordInputError, setPasswordInputError] = useState(false)

  const submitLogin = async () => {
    email === '' ? setEmailInputError(true) : setEmailInputError(false)
    password === '' ? setPasswordInputError(true) : setPasswordInputError(false)
    if (email !== '' && password !== '') {

      const reEmail = convertParameter(email)
      const rePassword = convertParameter(password)

      dispatch(startAction())
      try {
        const res = await agent.auth.login(reEmail, rePassword)
        dispatch(endAction())
        if (res.data.success) {
          localStorage.setItem('token', res.data.token)
          dispatch(showToast('success', res.data.message))
          dispatch(login(res.data))
          dispatch(startGame())
          navigate('/home')
        }
      } catch (error) {
        if(error.response != undefined){
          if (error.response.status === 401) {
            dispatch(endAction())
            dispatch(showToast('error', error.response.data.message))
          }
          if (error.response.status === 403) {
            dispatch(endAction())
            dispatch(showToast('error', error.response.data.message))
          }
        }
      }
    }
  }

  const convertParameter = (param) => {
    let convertParam = '';
    for(let i=0; i<param.length; i++) {
      if(param.charAt(i) == '#') {
        convertParam += '%23'
      } else if(param.charAt(i) == '&') {
        convertParam += '%26'
      } else {
        convertParam += param.charAt(i)
      }
    }

    return convertParam;
  }


  return (
    <div className={styles.main}>
      <div className={styles.login}>
        <div className={styles.login_card}>
          <div className={styles.login_title}>
            Entre com sua conta empresarial
          </div>
          <div className={styles.login_subtitle}>
            Voc precisa estar logado para ter acesso ao Universo
          </div>
          <input className={`${styles.login_input} ${styles.email} ${(emailInputError ? styles.error : '')}`} value={email} onChange={(e) => setEmail(e.target.value)} onKeyPress={(e) => { if (e.keyCode === 13 || e.charCode === 13) submitLogin() }} placeholder="Insira seu e-mail empresarial" />
          <input className={`${styles.login_input} ${styles.password}  ${(passwordInputError ? styles.error : '')}`} value={password} onChange={(e) => setPassword(e.target.value)} onKeyPress={(e) => { if (e.keyCode === 13 || e.charCode === 13) submitLogin() }} placeholder="Insira sua senha" type="password" />
          <div className={styles.login_forgot_btn_container}>
            <a className={styles.login_forgot_btn}>Esqueceu a senha?</a>
            <Link to={'/signup'} className={styles.login_forgot_btn}>Esqueceu a senha?</Link>
          </div>
          <div className={styles.login_confirm_btn_container}>
            <button id="submit_login" onClick={() => submitLogin()} className={styles.login_confirm_btn}>Iniciar Sesso</button>
          </div>
        </div>
      </div>
    </div >
  )
}

export default Login