import { useSelector } from 'react-redux'
import { TailSpin } from 'react-loader-spinner'

const PreloaderContainer = () => {
  const common = useSelector(state => state.common);
  return common.loading && <div style={{ position: 'absolute', top: '0px', left: '0px', width: '100%', height: '100%', backgroundColor: 'rgba(255,255,255,0.5)', zIndex: '10000' }}>
    <div style={{
      width: '100px',
      height: '100px',
      position: 'fixed',
      top: 'calc(50% - 50px)',
      left: 'calc(50% - 50px)',
      opacity: 1
    }}>
      <TailSpin color={'#E53222'} height={100} width={100} />
    </div>
  </div>
}

export default PreloaderContainer