import React, { useEffect, useState } from "react";
import { Link } from 'react-router-dom';

import styles from './Marketitem.module.scss';
import { getImg } from "../../../utils/Helper";

const Marketitem = (props) => {

	return (
		<Link to={`/university/detail/${props.value.id}`} >
			<div className={styles.market}>
				<div className={styles.market_img_container}>
					<img className={styles.market_img} src={props.value.courseImg} alt="Logo" />
				</div>
				<div className={styles.market_content_container}>
					<div className={styles.content_title}>
						{props.value.title}
					</div>
					<div className={styles.content}>
						{props.value.content}
					</div>
				</div>
				<div className={styles.subcontent_container}>
					<div className={styles.subcontent}>
						{props.value.count} Aulas&nbsp;&nbsp;&nbsp;&nbsp;{props.value.time} horas no total&nbsp;&nbsp;&nbsp;&nbsp;{props.value.text}
					</div>
				</div>
			</div>
		</Link>
	)
}

export default Marketitem