import React, { useState, useEffect } from "react";
import { Link, useNavigate } from 'react-router-dom';
import { useSelector, useDispatch } from 'react-redux'

import Sidemenu from "../../components/Sidemenu";
import RankingItem from "../../components/RankingItem";

import styles from './Ranking.module.scss';
import { getImg, useWindowSize } from "../../utils/Helper";

import {
  startAction,
  endAction,
  showToast
} from '../../actions/common'
import { logout } from '../../actions/auth'
import agent from '../../api/'

const Ranking = (props) => {
  const dispatch = useDispatch()
  const navigate = useNavigate()

  const [windowWidth, windowHeight] = useWindowSize();
  const [containerWidth, setContainerWidth] = useState(0);
  const [rankings, setRankings] = useState([]);

  useEffect(() => {
    async function getRankingData() {
      dispatch(startAction())
      try {
        const resRanking = await agent.common.getRanking()
        if (resRanking.data.success) setRankings([...resRanking.data.data])
        dispatch(endAction())
      } catch (error) {
        if (error.response.status === 401) {
          dispatch(endAction())
          dispatch(showToast('error', error.response.data.message))
          if (error.response.data.message == 'User token expired.') {
            localStorage.removeItem('token')
            dispatch(logout())
            window.dragon.stopUnityGame()
            navigate('/')
          }
        }
      }
    }
    getRankingData()
  }, [])

  const getWidth = (width) => {
    setContainerWidth(`calc(100% - ${width}px)`);
  }

  return (
    <div className={styles.main}>
      <div className={styles.ranking}>
        <Sidemenu getWidth={getWidth} active={2} />
        <div className={styles.ranking_container} style={{ width: containerWidth }}>
          <div className={styles.header}>
            <img src={getImg('image/icons/ranking_header.png')} alt="Logo" />
            <div className={styles.title}>
              Ranking
            </div>
          </div>
          <div className={styles.ranking_table}>
            <div className={styles.table_header}>
              <div className={styles.name}>Nome</div>
              <div className={styles.unit}>Unidade</div>
              <div className={styles.office}>Cargo</div>
              <div className={styles.time}>Tempo</div>
              <div className={styles.achivement}>Conquistas</div>
              <div className={styles.average}>Mdia</div>
            </div>
            <div className={styles.table_content}>
              {rankings.map((item, idx) => {
                if (idx % 2 === 1) {
                  return <RankingItem value={item} even={true} order={idx + 1} key={idx} />
                } else {
                  return <RankingItem value={item} even={false} order={idx + 1} key={idx} />
                }
              })}
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}

export default Ranking