import React, { useEffect, useState, useRef } from "react"
import { Link, useNavigate } from 'react-router-dom'
import { useSelector, useDispatch } from 'react-redux'
import { Navigate } from 'react-router'
import ArrowBackIosIcon from '@mui/icons-material/ArrowBackIos'
import ArrowForwardIosIcon from '@mui/icons-material/ArrowForwardIos'
import Menuitem from "./Menuitem"
import Notification from "./Notification"

import styles from './Sidemenu.module.scss'
import { getImg, useWindowSize } from "../../utils/Helper"

import agent from '../../api/'

import {
	startAction,
	endAction,
	showToast,
	showSmall
} from '../../actions/common'
import { logout } from '../../actions/auth'

const menulists = [
	{ id: 0, img: 'image/icons/Universe.png', text: 'Universo', page: 'home' },
	{ id: 1, img: 'image/icons/progress.png', text: 'Progresso', page: 'progress' },
	{ id: 2, img: 'image/icons/ranking.png', text: 'Ranking', page: 'ranking' },
	{ id: 3, img: 'image/icons/university.png', text: 'Universidade', page: 'university' },
	{ id: 4, img: 'image/icons/relative.png', text: 'Relatrio', page: 'report' },
	{ id: 5, img: 'image/icons/notification.png', text: 'Notificaes', page: '' },
	{ id: 6, img: 'image/icons/schedule.png', text: 'Agenda', page: 'schedule' },
	{ id: 7, img: 'image/icons/customize.png', text: 'Customizao', page: '' },
	{ id: 8, img: 'image/icons/logout.png', text: 'Logout', page: '' }
]

let menuWidth = 0;
let intervalID = 0;

const Sidemenu = (props) => {
	const navigate = useNavigate()
	const dispatch = useDispatch()
	const auth = useSelector(state => state.auth)
	const isSmall = useSelector(state => state.common.showSmall)

	const [showNotification, setShowNotification] = useState(false);
	const [notification, setNotification] = useState(0);
	const [notificationData, setNotificationData] = useState([]);

	const [windowWidth, windowHeight] = useWindowSize();

	useEffect(() => {
		const getData = async () => {
			console.log("interval id: ", intervalID)
			try {
				const resNotificationtData = await agent.common.getNotification()
				if (resNotificationtData.data.success) {
					setNotificationData([...resNotificationtData.data.data])
					let count = 0;
					resNotificationtData.data.data.map((alarm) => {
						if (!alarm.read) {
							count++;
						}
					})
					setNotification(count)
				}
			} catch (error) {
				if (error.response !== undefined && error.response.status === 401) {
					dispatch(showToast('error', error.response.data.message))
					if (error.response.data.message == 'User token expired.') {
						localStorage.removeItem('token')
						dispatch(logout())
						window.dragon.stopUnityGame()
						navigate('/')
					}
					clearInterval(intervalID);
					// console.log("interval id: ", intervalID)
					// intervalID = -1
				}
			}
		}
		getData()
		intervalID = setInterval(function () {
			getData()
			clearInterval(intervalID);
		}, 10000);
	}, [])

	useEffect(() => {
		if (isSmall) {
			menuWidth = 100;
			props.getWidth(menuWidth);
		} else {
			menuWidth = 266;
			props.getWidth(menuWidth);
		}
	}, [isSmall])

	const setNtf = (num) => {
		setNotification(num)
	}

	const toggleMenu = () => {
		(isSmall ? dispatch(showSmall(false)) : dispatch(showSmall(true)));
	}

	const toggleNotification = () => {
		setShowNotification((old) => old ? false : true)
	}

	const closeInterval = () => {
		return clearInterval(intervalID);
	}

	const getCustomizeData = async () => {
		try {
			const res = await agent.common.getCustomizeModel(auth.currentUser.id)
			if (res.data.success) {
				navigate('/none')
				window.dragon.clickCustomize(res.data.data)
			}
		} catch (error) {
			if (error.response !== undefined && error.response.status === 401) {
				dispatch(showToast('error', error.response.data.message))
				if (error.response.data.message == 'User token expired.') {
					localStorage.removeItem('token')
					dispatch(logout())
					window.dragon.stopUnityGame()
					navigate('/')
				}
			}
		}
	}

	return (
		<div className={(isSmall ? styles.small_sidemenu : styles.sidemenu)} style={{ height: windowHeight + 'px' }} >
			{isSmall &&
				<div className={styles.sidemenu_container}>
					<div className={styles.sidemenu_small_header}>
						<img className={styles.sidemenu_small_header_img} src={getImg('image/back/home_small.png')} alt="Logo" />
						<div className={styles.sidemenu_small_header_card}>
						</div>
					</div>
					<div className={styles.sidemenu_small_content}>
						{menulists.map((menulist, idx) => {
							if (props.active === idx) {
								return <Menuitem value={menulist} closeInterval={closeInterval} getCustomizeData={getCustomizeData} notification={notification} small={true} active={true} key={idx} />
							} else {
								return <Menuitem value={menulist} closeInterval={closeInterval} getCustomizeData={getCustomizeData} toggleNotification={toggleNotification} notification={notification} small={true} active={false} key={idx} />
							}
						})}
					</div>
				</div>
			}

			{!isSmall &&
				<div className={styles.sidemenu_container}>
					<div className={styles.sidemenu_header}>
						<div className={styles.sidemenu_header_effect_img_container}>
							<img className={styles.sidemenu_header_effect_img} src={getImg('image/back/effect.png')} alt="Logo" />
						</div>
						<div className={styles.sidemenu_header_img_container}>
							<img className={styles.sidemenu_header_img} src={getImg('image/back/home_small.png')} alt="Logo" />
						</div>
						<div className={styles.sidemenu_header_card_container}>

							<div className={styles.sidemenu_header_card}>
								<div className={styles.sidemenu_header_card_back}>
									<div className={styles.sidemenu_header_card_text}>
										Letcia M.
									</div>

								</div>
							</div>
						</div>
						<div className={styles.sidemenu_header_btn_container}>
							<Link to={'/profile'}>
								<button className={styles.sidemenu_header_btn}>Perfil</button>
							</Link>
						</div>
					</div>

					<div className={styles.sidemenu_content}>
						{menulists.map((menulist, idx) => {
							if (props.active === idx) {
								return <Menuitem value={menulist} closeInterval={closeInterval} getCustomizeData={getCustomizeData} toggleNotification={toggleNotification} notification={notification} small={false} active={true} key={idx} />
							} else {
								return <Menuitem value={menulist} closeInterval={closeInterval} getCustomizeData={getCustomizeData} toggleNotification={toggleNotification} notification={notification} small={false} active={false} key={idx} />
							}
						})}
					</div>
				</div>
			}

			{isSmall &&
				<div className={styles.sidemenu_small_btn_container}>
					<button onClick={toggleMenu} className={styles.sidemenu_small_btn}><ArrowForwardIosIcon sx={{ fontSize: 16 }} /></button>
				</div>
			}

			{!isSmall &&
				<div className={styles.sidemenu_small_btn_container}>
					<button onClick={toggleMenu} className={styles.sidemenu_small_btn}>< ArrowBackIosIcon sx={{ fontSize: 16 }} /></button>
				</div>
			}

			{
				showNotification && <Notification alarms={notificationData} toggleNotification={toggleNotification} />
			}
		</div >
	)
}

export default Sidemenu