import React, { useState, useEffect } from "react";
import { Link, useNavigate } from 'react-router-dom';
import { useSelector, useDispatch } from 'react-redux'

import VisibilityOffIcon from '@mui/icons-material/VisibilityOff';
import VisibilityIcon from '@mui/icons-material/Visibility';
import KeyIcon from '@mui/icons-material/Key';
import CreateIcon from '@mui/icons-material/Create';
import KeyboardArrowDownIcon from '@mui/icons-material/KeyboardArrowDown';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faLinkedinIn, faFacebookF, faInstagram, faTwitter } from '@fortawesome/free-brands-svg-icons';
import { faAngleLeft, faLock } from '@fortawesome/fontawesome-free-solid'


import styles from './EditInfo.module.scss';
import { getImg, useWindowSize } from "../../utils/Helper";

import {
  startAction,
  endAction,
  showToast
} from '../../actions/common'
import agent from '../../api/'
import { logout } from "../../actions/auth";

const validRegex = /^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/;

const CodeModal = (props) => {

  const auth = useSelector(state => state.auth)
  const dispatch = useDispatch()
  const navigate = useNavigate()

  const [code, setCode] = useState('')

  const conClick = async () => {
    if (code !== undefined && code !== null) {
      dispatch(startAction())
      const res = await agent.auth.sendScurityCode(auth.currentUser.id, code)
      if (res.data.success) {
        dispatch(showToast('success', res.data.message))
        props.setConfirmCode(true)
      } else {
        dispatch(showToast('error', res.data.message))
      }
      props.hidemodal()
      dispatch(endAction())
    }
  }

  return (
    <div className={styles.modal}>
      <div className={styles.lock_icon_container}>
        <div className={styles.lock_icon}>
          <FontAwesomeIcon icon={faLock} />
        </div>
      </div>
      <div className={styles.text_container}>
        <div className={styles.text}>
          Insira o Cdigo de autenticao gerado pelo seu Aplicativo
        </div>
      </div>
      <div className={styles.input_container}>
        <input type="text" placeholder="Cdigo de Segurana" value={code} onChange={(e) => setCode(e.target.value)} />
      </div>
      <div className={styles.confirm_btn_container}>
        <button onClick={() => conClick()}>Confirmar</button>
      </div>
    </div>
  )
}


const EditInfo = (props) => {
  const auth = useSelector(state => state.auth)
  const dispatch = useDispatch()
  const navigate = useNavigate()

  const [windowWidth, windowHeight] = useWindowSize();
  const [password, setPassword] = useState('');
  const [save, setSave] = useState(false);
  const [confirmEmail, setConfirmEmail] = useState('');
  const [emailVal, setEmailVal] = useState(false);
  const [confirmEmailVal, setConfirmEmailVal] = useState(false);
  const [info, setInfo] = useState({});
  const [avatarImg, setAvatarImg] = useState('');
  const [showModal, setShowModal] = useState(false);
  const [showPsw, setShowPsw] = useState(false);
  const [confirmCode, setConfirmCode] = useState(false);


  const hidemodal = () => {
    setShowModal(false)
  }


  const handleClick = async () => {
    if (!emailVal && !confirmEmailVal && confirmCode) {
      const res = await agent.auth.updateAccount(auth.currentUser.id, info.email, info.office, password, info.facebook, info.instagram, info.twitter, info.linkedin)
      if (res.data.success) {
        dispatch(showToast('success', res.data.message))
        // setSave(true)
      } else {
        dispatch(showToast('error', res.data.message))
      }
      setSave(true)
    }
  }

  const getCustomizeData = async () => {
    try {
      const res = await agent.common.getCustomizeModel(auth.currentUser.id)
      if (res.data.success) {
        navigate('/none')
        window.dragon.clickCustomize(res.data.data)
      }
    } catch (error) {
      if (error.response !== undefined && error.response.status === 401) {
        dispatch(showToast('error', error.response.data.message))
        if (error.response.data.message == 'User token expired.') {
          localStorage.removeItem('token')
          dispatch(logout())
          window.dragon.stopUnityGame()
          navigate('/')
        }
      }
    }
  }

  useEffect(() => {
    const getData = async () => {
      dispatch(startAction())
      try {
        const resAvatarImg = await agent.auth.getAccountImg(auth.currentUser.id)
        const resInfo = await agent.auth.getAccountData(auth.currentUser.id)
        if (resAvatarImg.data.success) setAvatarImg(resAvatarImg.data.data.avatarImg)
        if (resInfo.data.success) {
          setInfo({ ...resInfo.data.data })
          setPassword(resInfo.data.data.password)
        }
        dispatch(endAction())
      } catch (error) {
        if (error.response.status === 401) {
          dispatch(endAction())
          dispatch(showToast('error', error.response.data.message))
          if (error.response.data.message == 'User token expired.') {
            localStorage.removeItem('token')
            dispatch(logout())
            window.dragon.stopUnityGame()
            navigate('/')
          }
        }
      }
    }
    getData()
  }, [])

  useEffect(() => {
    if (info.email !== undefined && info.email !== null) {
      const validationEmail = info.email.match(validRegex)
      const validationCon = confirmEmail.match(validRegex)
      if (validationEmail && validationCon) {
        setEmailVal(info.email !== confirmEmail)
        setConfirmEmailVal(info.email !== confirmEmail)
      } else {
        setEmailVal(true)
        setConfirmEmailVal(true)
      }
    }
  }, [info, confirmEmail])

  return (
    <div className={styles.main}>
      <div className={styles.edit_info}>
        <div className={styles.edit_card}>
          <div className={styles.left_section}>
            <div className={styles.email}>
              <div className={styles.title}>
                E-mail
              </div>
              <div className={(!emailVal ? styles.input : styles.error_input)}>
                <input type="email" value={info.email} onChange={(e) => setInfo((oldInfo) => ({ ...oldInfo, email: e.target.value }))} />
                {emailVal &&
                  <>
                    <div className={styles.error_icon}>
                      <div className={styles.icon}>
                        !
                      </div>
                    </div>
                    <div className={styles.error_message}>
                      Insira um E-mail vlido
                    </div>
                  </>
                }
              </div>
            </div>
            <div className={styles.confirm_email}>
              <div className={styles.title}>
                Confirmar E-mail
              </div>
              <div className={(!confirmEmailVal ? styles.input : styles.error_input)}>
                <input type="email" value={confirmEmail} onChange={(e) => setConfirmEmail(e.target.value)} />
                {confirmEmailVal &&
                  <>
                    <div className={styles.error_icon}>
                      <div className={styles.icon}>
                        !
                      </div>
                    </div>
                    <div className={styles.error_message}>
                      Insira um E-mail vlido
                    </div>
                  </>
                }
              </div>
            </div>
            <div className={styles.office}>
              <div className={styles.title}>
                Cargo
              </div>
              <div className={styles.input}>
                <select value={info.office - 1} onChange={(e) => setInfo((oldInfo) => ({ ...oldInfo, office: e.target.value }))}>
                  <option value={0}>Aux. Administrativo</option>
                  <option value={1}>Aux. Administrativo1</option>
                  <option value={2}>2Aux. Administrativo2</option>
                </select>
                <div className={styles.select_icon}>
                  <KeyboardArrowDownIcon className={styles.arrow_down} />
                </div>
              </div>
            </div>
            <div className={styles.password}>
              <div className={styles.title}>
                Senha
              </div>
              <div className={styles.current}>
                <div className={styles.subtitle}>
                  Senha Atual
                </div>
                <div className={styles.input}>
                  {
                    !showPsw ?
                      <>
                        <input type="password" value={info.password} disabled />
                        <div className={styles.input_icon} onClick={() => setShowPsw(true)}>
                          <VisibilityOffIcon />
                        </div>
                      </> :
                      <>
                        <input type="text" value={info.password} disabled />
                        <div className={styles.input_icon} onClick={() => setShowPsw(false)}>
                          <VisibilityIcon />
                        </div>
                      </>
                  }

                </div>
              </div>

              <div className={styles.new}>
                <div className={styles.subtitle}>
                  Nova Senha
                </div>
                <div className={styles.input}>
                  <input type="text" value={password} onChange={(e) => setPassword(e.target.value)} />
                  <div className={styles.input_icon} onClick={() => setShowModal(true)}>
                    <KeyIcon />
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div className={styles.right_section}>
            <div className={styles.title}>
              Redes sociais:
            </div>
            <div className={styles.social_input}>
              <div className={styles.social_icon}>
                <FontAwesomeIcon icon={faFacebookF} />
              </div>
              <div className={styles.input}>
                <input type="text" value={info.facebook} onChange={(e) => setInfo((oldInfo) => ({ ...oldInfo, facebook: e.target.value }))} />
              </div>
            </div>

            <div className={styles.social_input}>
              <div className={styles.social_icon}>
                <FontAwesomeIcon icon={faInstagram} />
              </div>
              <div className={styles.input}>
                <input type="text" value={info.instagram} onChange={(e) => setInfo((oldInfo) => ({ ...oldInfo, instagram: e.target.value }))} />
              </div>
            </div>
            <div className={styles.social_input}>
              <div className={styles.social_icon}>
                <FontAwesomeIcon icon={faTwitter} />
              </div>
              <div className={styles.input}>
                <input type="text" value={info.twitter} onChange={(e) => setInfo((oldInfo) => ({ ...oldInfo, twitter: e.target.value }))} />
              </div>
            </div>
            <div className={styles.social_input}>
              <div className={styles.social_icon}>
                <FontAwesomeIcon icon={faLinkedinIn} />
              </div>
              <div className={styles.input}>
                <input type="text" value={info.linkedin} onChange={(e) => setInfo((oldInfo) => ({ ...oldInfo, linkedin: e.target.value }))} />
              </div>
            </div>
          </div>
          <div className={styles.save}>
            <div className={styles.save_container}>
              <Link to={'/profile'}>
                <div className={styles.prev_btn}>
                  <FontAwesomeIcon icon={faAngleLeft} />
                </div>
              </Link>
              <button className={styles.save_btn} onClick={() => handleClick()}>Salvar</button>
            </div>
          </div>
          {
            save &&
            <div className={styles.alert} onClick={() => setSave(false)}>
              Suas alteraes foram salvas
            </div>
          }
          {showModal && <CodeModal hidemodal={hidemodal} setConfirmCode={setConfirmCode} />}
        </div>
      </div>
      <Link to={'/home'}>
        <div className={styles.home_btn}>
          <img src={getImg('image/icons/home.png')} alt="icon" />
        </div>
      </Link>

      <div className={styles.avatar_container}>
        <div className={styles.rel_container}>
          {/* <img src={avatarImg} alt="Logo" /> */}
          <img src={getImg('image/back/avatar.png')} alt="Logo" />
          <div className={styles.edit_btn} onClick={() => getCustomizeData()}>
            <CreateIcon />
          </div>
        </div>
      </div>

      {/* <div className={styles.avatar}>
        <img src={getImg('image/back/video_back.png')} alt="icon" />
      </div> */}
    </div >
  )
}

export default EditInfo